"use client"

import { useState, useRef, useEffect } from "react"
import Image from "next/image"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"

const products = [
  {
    id: 1,
    title: "SOFT LEATHER JACKETS",
    description: "Scelerisque duis aliquam qui lorem ipsum dolor amet, consectetur adipiscing elit.",
    image: "/placeholder.svg?height=600&width=500",
  },
  {
    id: 2,
    title: "SOFT LEATHER JACKETS",
    description: "Scelerisque duis aliquam qui lorem ipsum dolor amet, consectetur adipiscing elit.",
    image: "/placeholder.svg?height=600&width=500",
  },
  {
    id: 3,
    title: "SOFT LEATHER JACKETS",
    description: "Scelerisque duis aliquam qui lorem ipsum dolor amet, consectetur adipiscing elit.",
    image: "/placeholder.svg?height=600&width=500",
  },
  {
    id: 4,
    title: "SOFT LEATHER JACKETS",
    description: "Scelerisque duis aliquam qui lorem ipsum dolor amet, consectetur adipiscing elit.",
    image: "/placeholder.svg?height=600&width=500",
  },
  {
    id: 5,
    title: "SOFT LEATHER JACKETS",
    description: "Scelerisque duis aliquam qui lorem ipsum dolor amet, consectetur adipiscing elit.",
    image: "/placeholder.svg?height=600&width=500",
  },
]

export default function ProductCarousel() {
  const [currentIndex, setCurrentIndex] = useState(0)
  const maxIndex = Math.max(0, products.length - 3)
  const containerRef = useRef(null)

  const nextSlide = () => {
    setCurrentIndex((prevIndex) => Math.min(prevIndex + 1, maxIndex))
  }

  const prevSlide = () => {
    setCurrentIndex((prevIndex) => Math.max(prevIndex - 1, 0))
  }

  useEffect(() => {
    if (containerRef.current) {
      const scrollAmount = currentIndex * (containerRef.current.scrollWidth / products.length)
      containerRef.current.scrollTo({
        left: scrollAmount,
        behavior: "smooth",
      })
    }
  }, [currentIndex])

  return (
    <div className="relative">
      <div className="absolute left-0 top-1/2 -translate-y-1/2 z-10">
        <Button
          variant="outline"
          size="icon"
          className="rounded-full bg-background/80 backdrop-blur-sm"
          onClick={prevSlide}
          disabled={currentIndex === 0}
        >
          <ChevronLeft className="h-6 w-6" />
          <span className="sr-only">Previous slide</span>
        </Button>
      </div>
      <div ref={containerRef} className="flex overflow-x-hidden gap-6 px-10">
        {products.map((product) => (
          <div key={product.id} className="min-w-[300px] flex-1">
            <div className="group relative overflow-hidden rounded-lg">
              <div className="relative h-[400px] w-full overflow-hidden">
                <Image
                  src={product.image || "/placeholder.svg"}
                  alt={product.title}
                  fill
                  className="object-cover transition-all duration-500 group-hover:scale-110"
                  style={{
                    transform: "scale(0.85)",
                    transition: "transform 0.5s ease-in-out",
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.transform = "scale(0.95)"
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.transform = "scale(0.85)"
                  }}
                />
              </div>
            </div>
            <div className="mt-4">
              <h3 className="text-lg font-medium">{product.title}</h3>
              <p className="mt-2 text-sm text-muted-foreground">{product.description}</p>
              <Button className="mt-4 bg-black hover:bg-gray-800 text-white">Add Cart</Button>
            </div>
          </div>
        ))}
      </div>
      <div className="absolute right-0 top-1/2 -translate-y-1/2 z-10">
        <Button
          variant="outline"
          size="icon"
          className="rounded-full bg-background/80 backdrop-blur-sm"
          onClick={nextSlide}
          disabled={currentIndex === maxIndex}
        >
          <ChevronRight className="h-6 w-6" />
          <span className="sr-only">Next slide</span>
        </Button>
      </div>
    </div>
  )
}

